.detail_container {
  position: absolute;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #212121;
}

.detail_poster {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.detail_info {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  color: aliceblue;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

span {
  font-size: 50px;
  font-weight: bold;
}

p {
  font-size: 20px;
}

.detail_genres {
  font-size: 15px;
  font-weight: 500;
  list-style: none;
  display: flex;
  padding-left: 0;
}

.detail_genre {
  background-color: slategray;
  border-radius: 10px;
  margin: 5px;
  padding: 5px;
  text-align: center;
}
