div .nav {
  width: 100%;
  z-index: 1;
  position: fixed;
  left: 0;
  background-color: black;
  padding: 10px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

a {
  padding-right: 10px;
  text-decoration: none;
  color: aliceblue;
  font-size: 20px;
  font-weight: 500;
}

.about {
  position: absolute;
  top: 50px;
}

.nav_title {
  padding-right: 20px;
  color: coral;
}

.nav_home:hover,
.nav_about:hover {
  color: coral;
}
